.OuterContainer {
    background-color: black;
    color: grey;
    display: flex;
    justify-content: center;
}

.InnerContainer {
    width: 65%;
}

.Icons {
    width: 12%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    color: #fff;
}

.FooterData {
    display: flex;
    justify-content: space-between;
}

.FooterData ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 12vh;
}

.FooterData ul li {
    cursor: pointer;
}

