 .Banner {
    /* margin-top: 10vh; */
    height: 80vh;
    margin-bottom: -80px;
    color: beige;
    object-fit: contain;
 }

.BannerInfo {
    margin-left: 30px;
    padding-top: 300px;
}

.BannerTitle {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.BannerDescription {
    width: 45rem;
    /* line-height: 1; */
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}

.Buttons {
    color: white;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 1rem;
    /* background-color: rgba(51, 51, 51, 0.5); */
    /* transition: background-color 0.3s ease; */

}
.PlayButton {
    color: white;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 1rem;
}

.MoreInforButton {
    color: grey;
    text-emphasis-color: black;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 1rem;
}

